<a-form layout="vertical"><a-form-item label="题目"><Editor v-model="dataClone.stem" @mounted="onEditorMounted(0, $event)"></Editor></a-form-item><a-form-item label="选项"><div class="sm-item" v-for="(meta,metaIndex) in dataClone.pMetas" :key="meta.uid"><a-checkbox :checked="isChecked(metaIndex)" @click.native.stop="handleCheckboxChange(metaIndex)"></a-checkbox><a-input v-model:value="meta.meta" :maxlength="50" :minlength="1"></a-input><DeleteOutlined class="sm-del btn-gray" @click.native.stop="handleDelMetaClick(metaIndex)" type="link" size="small"></DeleteOutlined></div><div class="flex justify-center"><a-button class="sm-add-meta" block @click="handleAddMetaClick" type="dashed"><PlusOutlined></PlusOutlined><span>添加选项</span></a-button></div></a-form-item><a-form-item label="题目分值"><a-input-number v-model:value="dataClone.score" :max="20" :min="1"></a-input-number></a-form-item><a-form-item label="解析"><Editor v-model="dataClone.analysis" @mounted="onEditorMounted(1, $event)"></Editor></a-form-item></a-form>