<a-form layout="vertical"><a-form-item label="题目"><Editor v-model="dataClone.stem" ref="stem" @mounted="onEditorMounted(0, $event)"></Editor></a-form-item><a-form-item label="选项"><a-radio-group v-model:value="dataClone.pAnswer[0]"><div class="smi-wrap" v-for="(meta,metaIndex) in dataClone.pMetas" :key="meta.uid"><a-radio class="sm-item" :value="metaIndex" size="large"><a-input v-model:value="meta.meta" :maxlength="50" :minlength="1"></a-input></a-radio><span class="sm-del" :class="reachMinLimit?'disabled':''"><DeleteOutlined class="btn-gray" @click.native.stop="handleDelMetaClick(metaIndex)" type="link" size="small"></DeleteOutlined></span></div></a-radio-group><div class="flex justify-center"><a-button class="sm-add-meta" block @click="handleAddMetaClick" type="dashed" :disabled="reachMaxLimit"><PlusOutlined></PlusOutlined><span>添加选项</span></a-button></div></a-form-item><a-form-item label="题目分值"><a-input-number v-model:value="dataClone.score" :max="20" :min="1"></a-input-number></a-form-item><a-form-item label="解析"><Editor v-model="dataClone.analysis" @mounted="onEditorMounted(1, $event)"></Editor></a-form-item></a-form>