<a-form layout="vertical"><a-form-item label="题目"><Editor v-model="dataClone.stem" @mounted="onEditorMounted(0, $event)"></Editor></a-form-item><a-form-item label="选项"><a-radio-group v-model:value="dataClone.pAnswer[0]"><a-radio :value="true">正确</a-radio><a-radio :value="false">错误</a-radio></a-radio-group></a-form-item><a-form-item label="题目分值"><a-input-number v-model:value="dataClone.score" :max="20" :min="1"></a-input-number></a-form-item><a-form-item label="解析"><Editor v-model="dataClone.analysis" @mounted="onEditorMounted(1, $event)"></Editor></a-form-item></a-form>