<a-modal class="qu-wrap" :visible="visible" width="800px" @cancel="handleCancel"><template #title><div class="qu-title">编辑题目</div></template><a-form layout="vertical"><a-form-item label="题型" :wrapperCol="{span:8}"><a-select v-model:value="dataClone.questionType" @change="handleQTypeChange($event,dataClone)"><a-option v-for="(q,qIndex) in questionTypes" :value="qIndex">{{q.name}}</a-option></a-select></a-form-item></a-form><component :is="currentComponent(dataClone.questionType)" @emit-validate="handleEmitValidate"></component><template #footer><a-button class="mr8" @click="handleCancel">取消</a-button><a-button type="primary" @click="handleOk" :loading="loading">确定</a-button></template></a-modal>